<template>
  <div class="row" v-if="loaded">
    <div class="col">
      <b-form class="form-signin" @submit.prevent="submit">
        <div class="row">
          <div class="col-md-6">
            <b-form-group label="Name">
              <b-form-input type="text" placeholder="Name" required autofocus autocomplete v-model="name" />
            </b-form-group>
          </div>
        </div>
        <div class="row">
          <div class="col-md-12">
            <b-form-group label="Description">
              <b-form-input type="text" placeholder="Description" required autocomplete v-model="description" />
            </b-form-group>
          </div>
        </div>
        <div class="row">
          <div class="col-md-6">
            <b-form-group label="Price Order">
              <b-form-input type="number" placeholder="Price Order" required autocomplete v-model="priceOrder" />
            </b-form-group>
          </div>
        </div>
        <div class="row">
          <div class="col-md-6">
            <b-form-group label="Service Category">
              <b-form-select v-model="serviceCategoryId" :options="list.serviceCategories" value-field="serviceCategoryId" text-field="name" />
            </b-form-group>
          </div>
          <div class="col-md-6">
            <b-form-group label="Service Type">
              <b-form-select v-model="serviceTypeId" :options="list.serviceTypes" value-field="serviceTypeId" text-field="name" />
            </b-form-group>
          </div>
        </div>
        <hr class="mb-4" />
        <div class="row">
          <div class="col">
            <b-button v-b-modal.zoneModal>Add Zone</b-button>
            <b-table striped hover small :items="zones.items" :fields="zones.fields">
              <template v-slot:cell(action)="row">
                <a href="#" @click.prevent="removeZone(row)">delete</a>
              </template>
            </b-table>
          </div>
        </div>
        <hr class="mb-4" />
        <div class="row">
          <div class="col">
            <b-button v-b-modal.segmentModal>Add Segments</b-button>
            <b-table striped hover small :items="segments.items" :fields="segments.fields">
              <template v-slot:cell(action)="row">
                <a href="#" @click.prevent="removeSegment(row)">delete</a>
              </template>
            </b-table>
          </div>
        </div>
        <hr class="mb-4" />
        <b-button squared block variant="dark" type="submit">Save</b-button>
      </b-form>
    </div>
    <b-modal id="zoneModal" title="Available Zones" ok-only>
      <b-table striped hover small :items="availableZones" :fields="list.zones.fields">
        <template v-slot:cell(action)="row">
          <a href="#" @click.prevent="addZone(row)">add</a>
        </template>
      </b-table>
    </b-modal>
    <b-modal id="segmentModal" title="Available Segments" ok-only>
      <b-table striped hover small :items="availableSegments" :fields="list.segments.fields">
        <template v-slot:cell(action)="row">
          <a href="#" @click.prevent="addSegment(row)">add</a>
        </template>
      </b-table>
    </b-modal>
  </div>
</template>

<script>
export default {
  props: {
    productType: String,
    productId: String,
  },
  computed: {
    isNew() {
      return this.productId == "new";
    },
    availableZones() {
      let available = this.list.zones.items.filter((x) => {
        return (
          this.zones.items.filter((y) => {
            return y.zoneId == x.zoneId;
          }).length == 0
        );
      });
      return available;
    },
    availableSegments() {
      let available = this.list.segments.items.filter((x) => {
        return (
          this.segments.items.filter((y) => {
            return y.segmentId == x.segmentId;
          }).length == 0
        );
      });
      return available;
    },
  },
  async mounted() {
    await this.load();
  },
  methods: {
    async load() {
      try {
        let query = {
          productType: "standalone",
          includes:
            "filterlist.servicetypes,filterlist.servicecategories,filterlist.zones,filterlist.segments",
        };
        let url = `BackendServices/Products/${this.productId}`;
        let response = await this.$http.get(url, { query: query });

        this.name = response.name;
        this.description = response.description;
        this.priceOrder = parseFloat(response.priceOrder);
        this.serviceCategoryId = response.serviceCategoryId;
        this.serviceTypeId = response.serviceTypeId;
        this.zones.items = response.zones;
        this.segments.items = response.segments;

        this.list.serviceCategories = response.filterList.serviceCategories;
        this.list.serviceTypes = response.filterList.serviceTypes;
        this.list.zones.items = response.filterList.zones;
        this.list.segments.items = response.filterList.segments;

        this.loaded = true;
      } catch (ex) {
        this.$event.$emit("error", ex.message);
      }
    },
    addZone(row) {
      let zone = this.list.zones.items.filter((x) => {
        return x.zoneId == row.item.zoneId;
      })[0];

      this.zones.items.push({
        zoneId: zone.zoneId,
        name: zone.name,
      });
    },
    removeZone(row) {
      this.zones.items.splice(row.index, 1);
    },
    addSegment(row) {
      let segment = this.list.segments.items.filter((x) => {
        return x.segmentId == row.item.segmentId;
      })[0];

      this.segments.items.push({
        segmentId: segment.segmentId,
        name: segment.name,
      });
    },
    removeSegment(row) {
      this.segments.items.splice(row.index, 1);
    },
    async submit() {
      if (this.isNew) {
        await this.add();
      } else {
        await this.update();
      }
    },
    async add() {
      try {
        let zones = this.zones.items.map((x) => {
          return {
            zoneId: x.zoneId,
          };
        });

        let segments = this.segments.items.map((x) => {
          return {
            segmentId: x.segmentId,
          };
        });

        let body = {
          name: this.name,
          description: this.description,
          priceOrder: parseFloat(this.priceOrder),
          serviceCategoryId: this.serviceCategoryId,
          serviceTypeId: this.serviceTypeId,
          zones: zones,
          segments: segments,
        };
        let url = `BackendServices/Products`;
        await this.$http.post(url, { body });

        this.$router.push({ name: "products" });
      } catch (ex) {
        this.$event.$emit("error", ex.message);
      }
    },
    async update() {
      try {
        let zones = this.zones.items.map((x) => {
          return {
            zoneId: x.zoneId,
          };
        });

        let segments = this.segments.items.map((x) => {
          return {
            segmentId: x.segmentId,
          };
        });

        let body = {
          name: this.name,
          description: this.description,
          priceOrder: parseFloat(this.priceOrder),
          serviceCategoryId: this.serviceCategoryId,
          serviceTypeId: this.serviceTypeId,
          zones: zones,
          segments: segments,
        };
        let url = `BackendServices/Products/${this.productId}`;
        await this.$http.patch(url, { body });

        this.$router.push({ name: "products" });
      } catch (ex) {
        this.$event.$emit("error", ex.message);
      }
    },
  },
  data() {
    return {
      name: "",
      description: "",
      priceOrder: 0,
      serviceCategoryId: "",
      serviceTypeId: "",
      zones: {
        items: [],
        fields: [{ key: "name" }, { key: "action", label: "" }],
      },
      segments: {
        items: [],
        fields: [{ key: "name" }, { key: "action", label: "" }],
      },
      list: {
        serviceCategories: [],
        serviceTypes: [],
        zones: {
          items: [],
          fields: [{ key: "name" }, { key: "action", label: "" }],
        },
        segments: {
          items: [],
          fields: [{ key: "name" }, { key: "action", label: "" }],
        },
      },
      loaded: false,
    };
  },
};
</script>